/*
ajax
axios
promise
 */
import axios from 'axios'
export default function ajax(url,data={},type='GET',config={}){
    if(type==='GET'){
        return axios.get(url,{
            params:data
        });
    }else{
        if(JSON.stringify(config)==='{}'){
            return axios.post(url,data);
        }
        return axios.post(url,data,config);
    }
}